/* eslint-disable no-alert */
import React, { useRef } from 'react'
import { Link, navigate } from 'gatsby'
import axios from 'axios'

import helpers from '../../../helpers'

import logoPath from '../../../images/logoLogin.svg'

import styles from './Login.module.scss'

const { linksPaths } = helpers.constants.links
const { POST_GET_AUTH } = helpers.api

function Login() {
    const loginField = useRef(null)
    const passwordField = useRef(null)
    return (
        <div className={styles.layout}>
            <header className={styles.header}>
                <Link to={linksPaths.root}>
                    <img src={logoPath} alt="logo" />
                </Link>
            </header>
            <form
                className={styles.form}
                onSubmit={(e) => {
                    e.preventDefault()
                    axios.post(POST_GET_AUTH, {
                        login: loginField.current.value,
                        password: passwordField.current.value,
                    }, { withCredentials: true })
                        .then(() => navigate('/briefcase/'))
                }}
            >
                <label className={styles.form__label}>
                    <span>Логин</span>
                    <input className={styles.form__input} type="text" name="login" id="login" ref={loginField} />
                </label>
                <label className={styles.form__label}>
                    <span>Пароль</span>
                    <input className={styles.form__input} type="password" name="password" id="password" ref={passwordField} />
                </label>
                <button className={styles.form__button} type="submit">
                    Войти
                </button>
            </form>
        </div>
    )
}

export default Login
